import { Controller, Commands } from '@ali/4ever-cangjie';
import { merge } from 'lodash-es';

export default function updateDivider(editor: Controller, node: any, data) {
  const merged = {};
  // merge 解决undefined的字段不应该覆盖原有值
  merge(merged, node.data, data);
  return editor
    .command(Commands.setNodeByKey, node.key, {
      data: {
        ...merged,
      },
    });
}
